import React, { useState } from "react";
import { Link, NavLink } from "react-router-dom";
import { Button } from "react-bootstrap";
import sidebtn from "../asset/image/sidebar/sidebar-btn.svg";
import logo from "../asset/image/header/logo.png";
import DarkMode from "./darkmode";
import SocialMedia from "./socialmedia";
import "./layout.css";
import SpinWheel from "../spinWheel/spinWheel";

const SideBar = ({ sidebarToggle, handleSidebar }) => {
  const places = [
    "100",
    "200",
    "300",
    "400",
    "500",
    "600",
    "700",
    "800",
    "900",
    "1000",
    "1100",
    "1200",
    "1300",
    "1400",
    "1500",
    "1600",
  ];

  const [showSpinner, setShowSpinner] = useState(false);

  const handleClose = () => setShowSpinner(false);
  const handleShow = () => setShowSpinner(true);

  return (
    <div className="sidebar-content">
      <div className="sidebar-logo">
        <Link to="/">
          <img src={logo} alt="logo" />
        </Link>
        <div className="mini-sidebar-btn">
          <Button onClick={handleSidebar}>
            <img src={sidebtn} alt="icon" />
          </Button>
        </div>
      </div>
      <div className="sidebar-menu-list">
        <NavLink to="/">
          <Home />
          Home
        </NavLink>

        <NavLink to="/">
          <Original />
          SG Originals
        </NavLink>

        <Button onClick={handleShow} className="nav-spin-btn">
          <FreeSpin /> Free Spin
        </Button>

        <NavLink to="/">
          <Promotion />
          Promotion
        </NavLink>

        <NavLink to="/">
          <Contact />
          Contact
        </NavLink>

        <NavLink to="/">
          <Play />
          How To Play
        </NavLink>

        <NavLink to="/">
          <Faq />
          FAQ
        </NavLink>

        <NavLink to="/">
          <Support />
          Support
        </NavLink>
      </div>
      <DarkMode />
      <SocialMedia />
      <SpinWheel items={places} handleClose={handleClose} show={showSpinner} />
    </div>
  );
};
export default SideBar;

